/**
 * @copyright Microsoft Corporation. All rights reserved.
 *
 * @file MobilePreview
 * Shows a view of the user's page in a mobile device-sized iframe.
 * The frame size is configurable through click stops set to the size of popular device
 * forms, X and Y dimension input boxes, and options to rotate the device and switch
 * between mobile, tablet and desktop sizes. There is also an option to view an alternate
 * URL of the user's input in the frame.
 */

import * as React from 'react';
import { css } from 'office-ui-fabric-react';

import styles from './MobilePreview.module.scss';
import { mobilePreviewDevices as importedDevices } from '../MobilePreviewDevices';
import MobilePreviewClickStopBar, {
  IMobilePreviewClickStopBarProps
} from '../mobilePreviewClickStopBar/MobilePreviewClickStopBar';
import MobilePreviewHeaderBar, {
  IMobilePreviewHeaderBarProps
} from '../mobilePreviewHeaderBar/MobilePreviewHeaderBar';
import MobilePreviewDeviceView, {
  IMobilePreviewDeviceViewProps
} from '../mobilePreviewDeviceView/MobilePreviewDeviceView';
import MobilePreviewDimensionInput, {
  IMobilePreviewDimensionInputProps
} from '../mobilePreviewDimensionInput/MobilePreviewDimensionInput';
import MobilePreviewRotate, {
  IMobilePreviewRotateProps
} from '../mobilePreviewRotate/MobilePreviewRotate';

export enum DeviceType {
  Phone,
  Tablet
}

export enum DeviceOrientation {
  Portrait,
  Landscape
}

export interface IMobilePreviewProps {
  deviceType?: DeviceType;
  onExit?: () => void;
  devices?: IDeviceList;
}

/**
 * Example:
 * state = {
 *  currentDevice: { name: 'Windows Lumia 950', width: 360, height: 640 },
 *  currentOrientation: DeviceOrientation.Portrait,
 *  deviceType: DeviceType.Phone,
 *  url: 'http://www.bing.com'
 * }
 */
export interface IMobilePreviewState {
  currentDevice: IDevice;
  currentOrientation: DeviceOrientation;
  deviceType: DeviceType;
  url: string;
}

export interface IDevice {
  name: string;
  width: number;
  height: number;
}

export interface IDeviceList {
  phones: IDevice[];
  tablets: IDevice[];
  desktops: IDevice[];
}

export default class MobilePreview extends React.Component<IMobilePreviewProps, IMobilePreviewState> {

  private defaultPhone: IDevice;
  private defaultTablet: IDevice;
  private devices: IDeviceList;

  // Store string from url textfield outside of state so we don't change the iframe URL until the user clicks submit
  private inProgressURL: string = '';

  constructor(props: IMobilePreviewProps) {
    super(props);

    if (!this.props.devices) {
      this.devices = importedDevices;
    } else {
      this.devices = this.props.devices;
    }

    const deviceType: DeviceType = this.props.deviceType || DeviceType.Phone;

    /**
     * Pick a default device for now, which will be changed later
     * A current device is needed before sorting can happen
    */
    this.defaultPhone = this.devices.phones[this.devices.phones.length - 1];
    this.defaultTablet = this.devices.tablets[this.devices.tablets.length - 1];

    this.state = {
      currentOrientation: deviceType === DeviceType.Phone ?
          DeviceOrientation.Portrait : DeviceOrientation.Landscape,
      currentDevice: deviceType === DeviceType.Phone ? this.defaultPhone : this.defaultTablet,
      deviceType: deviceType,
      url: window.location.href
    };

    this._onClickstopChangeForm = this._onClickstopChangeForm.bind(this);
    this._onClickRotate = this._onClickRotate.bind(this);
    this._onClickChangeDeviceType = this._onClickChangeDeviceType.bind(this);
    this._onChangedX = this._onChangedX.bind(this);
    this._onChangedY = this._onChangedY.bind(this);
    this._onChangedURLField = this._onChangedURLField.bind(this);
    this._onClickURL = this._onClickURL.bind(this);
    this._sortDevices = this._sortDevices.bind(this);
    this._comparePhones = this._comparePhones.bind(this);
    this._compareTablets = this._compareTablets.bind(this);

    this._sortDevices();
    this.state.currentDevice = props.deviceType === DeviceType.Phone ? this.defaultPhone : this.defaultTablet;
  }

  public render(): React.ReactElement<{}> {

    this._sortDevices();

    const mobilePreviewClickStopBar: React.ReactElement<IMobilePreviewClickStopBarProps> =
      <MobilePreviewClickStopBar
        currentDevice={ this.state.currentDevice }
        currentOrientation={ this.state.currentOrientation }
        devices={ this.state.deviceType === DeviceType.Phone ? this.devices.phones : this.devices.tablets }
        deviceType={ this.state.deviceType }
        onSelectClickStop={ this._onClickstopChangeForm }
      />;

    const mobilePreviewHeaderBar: React.ReactElement<IMobilePreviewHeaderBarProps> =
      <MobilePreviewHeaderBar
        onChangeURL={ this._onChangedURLField }
        onExit={ this.props.onExit }
        onClickURLSubmit={ this._onClickURL }
        onChangeDeviceType={ this._onClickChangeDeviceType }
      />;

    const mobilePreviewDeviceView: React.ReactElement<IMobilePreviewDeviceViewProps> =
      <MobilePreviewDeviceView
        currentDevice={ this.state.currentDevice }
        currentOrientation={ this.state.currentOrientation }
        deviceType={ this.state.deviceType }
        url={ this.state.url }
      />;

    const mobilePreviewDimensionInput: React.ReactElement<IMobilePreviewDimensionInputProps> =
      <MobilePreviewDimensionInput
        currentDevice={ this.state.currentDevice }
        onChangedX={ this._onChangedX }
        onChangedY={ this._onChangedY }
      />;

    const mobilePreviewRotate: React.ReactElement<IMobilePreviewRotateProps> =
      <MobilePreviewRotate
        onRotate={ this._onClickRotate }
        currentOrientation={ this.state.currentOrientation }
      />;

    const containerClass: string = css(styles.mobilePreviewContainer, 'ms-Grid');

    return (
      <div className={ containerClass }>
        { mobilePreviewHeaderBar }
        { mobilePreviewClickStopBar }
        <div className={ styles.mobilePreviewDeviceContainer }>
          { mobilePreviewDimensionInput }
          { mobilePreviewRotate }
          { mobilePreviewDeviceView }
        </div>
      </div>
    );
  }

  private _onClickstopChangeForm(device: IDevice): void {
    this.setState({
      currentDevice: device,
      currentOrientation: this.state.currentOrientation,
      deviceType: this.state.deviceType,
      url: this.state.url
    });
  }

  private _onClickRotate(orientation: DeviceOrientation): void {
    this.setState({
      currentDevice: this.state.currentDevice,
      currentOrientation: orientation,
      deviceType: this.state.deviceType,
      url: this.state.url
    });
  }

  private _onClickChangeDeviceType(type: DeviceType): void {
    this.setState({
      currentDevice: type === DeviceType.Phone ? this.defaultPhone : this.defaultTablet,
      currentOrientation: type === DeviceType.Phone ? DeviceOrientation.Portrait : DeviceOrientation.Landscape,
      deviceType: type,
      url: this.state.url
    });
  }

  private _onChangedX(event: React.FormEvent): void {
    const heightTarget: HTMLInputElement = event.target as HTMLInputElement;
    const customDevice: IDevice = {
      name: 'Custom Device',
      width: +heightTarget.value,
      height: this.state.currentDevice.height
    };
    this.setState({
      currentDevice: customDevice,
      currentOrientation: this.state.currentOrientation,
      deviceType: this.state.deviceType,
      url: this.state.url
    });
  }

  private _onChangedY(event: React.FormEvent): void {
    const heightTarget: HTMLInputElement = event.target as HTMLInputElement;
    const customDevice: IDevice = {
      name: 'Custom Device',
      height: +heightTarget.value,
      width: this.state.currentDevice.width
    };
    this.setState({
      currentDevice: customDevice,
      currentOrientation: this.state.currentOrientation,
      deviceType: this.state.deviceType,
      url: this.state.url
    });
  }

  private _onChangedURLField(value: string): void {
    this.inProgressURL = value;
  }

  private _onClickURL(): void {
    if (this.inProgressURL === '' || this.inProgressURL === undefined) {
      return;
    }
    this.setState({
      currentDevice: this.state.currentDevice,
      currentOrientation: this.state.currentOrientation,
      deviceType: this.state.deviceType,
      url: this.inProgressURL
    });
  }

  /**
   * Used to sort devices by either height or width depending on the orientation of the device. This is necessary
   * because the clickstops will only show up/function properly if they are rendered in order from greatest to
   * smallest length.
   */
  private _sortDevices(): void {
    if (this.state.deviceType === DeviceType.Phone) {
      this.devices.phones.sort(this._comparePhones);
      this.defaultPhone = this.devices.phones[this.devices.phones.length - 1];
    } else {
      this.devices.tablets.sort(this._compareTablets);
      this.defaultTablet = this.devices.tablets[this.devices.tablets.length - 1];
    }
  }

  private _comparePhones(deviceA: IDevice, deviceB: IDevice): number {
    let aMeasure: number = deviceA.width;
    let bMeasure: number = deviceB.width;
    if (this.state.currentOrientation === DeviceOrientation.Landscape) {
      aMeasure = deviceA.height;
      bMeasure = deviceB.height;
    }

    return bMeasure - aMeasure;
  }

  private _compareTablets(deviceA: IDevice, deviceB: IDevice): number {
    let aMeasure: number = deviceA.height;
    let bMeasure: number = deviceB.height;
    if (this.state.currentOrientation === DeviceOrientation.Landscape) {
      aMeasure = deviceA.width;
      bMeasure = deviceB.width;
    }

    return bMeasure - aMeasure;
  }

}
